
'use client';

import LoadingLink from '@/components/loading-link';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Eye, LogIn as LogInIcon, AlertCircle, Loader2, LogOut as LogOutIcon, LayoutDashboard, UserPlus } from 'lucide-react';
import { useRouter } from 'next/navigation';
import React, { useEffect, useState, useActionState } from 'react';
import { login, getSessionUser } from '@/lib/actions';
import { useLoading } from '@/contexts/loading-context';

export default function LoginPage() {
  const router = useRouter();
  const [isDebugMode, setIsDebugMode] = useState(false);
  const [isLoggedIn, setIsLoggedIn] = useState<boolean | null>(null);
  const [sessionUsername, setSessionUsername] = useState<string>('');
  const { startLoading, stopLoading } = useLoading();

  const initialState = { message: null, errors: {}, success: false };
  const [state, formAction] = useActionState(
    async (prevState: any, formData: FormData) => {
      startLoading();
      let result;
      try {
        result = await login(prevState, formData);

        if (result && (result.errors || (result.message && !result.success))) {
          stopLoading(); // Stop loading if form submission fails and there's no redirect
        }
        // If successful, redirect is handled by Next.js, loading bar stops on route change
        return result;
      } catch (error: any) {
        if (error && typeof error === 'object' && 'digest' in error && typeof error.digest === 'string' && error.digest.startsWith('NEXT_REDIRECT')) {
          // Re-throw NEXT_REDIRECT error to be handled by Next.js
          throw error;
        }
        console.error("Login action failed on client:", error);
        stopLoading();
        return { message: '登录或注册时发生意外错误。', errors: {}, success: false };
      }
    },
    initialState
  );

  useEffect(() => {
    setIsDebugMode(process.env.NEXT_PUBLIC_DEBUG_MODE === 'true');

    async function checkLoginStatus() {
      try {
        const user = await getSessionUser();
        if (user) {
          setIsLoggedIn(true);
          setSessionUsername(user.username);
        } else {
          setIsLoggedIn(false);
        }
      } catch (e) {
         console.error("登录页面检查登录状态时出错:", e);
         setIsLoggedIn(false);
      }
    }
    checkLoginStatus();
  }, []);

  const handleDebugLogin = () => {
    startLoading();
    router.push('/dashboard?debug_user_id=dream_pep_id');
  };
  
  const handleLogout = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    startLoading();
    event.currentTarget.submit();
  };


  if (isLoggedIn === null) {
    return (
      <div className="flex justify-center items-center min-h-[calc(100vh-15rem)]"> {/* Adjust min-height if needed within new layout */}
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
      </div>
    );
  }
  
  // Main content container for login page to handle its own centering
  const LoginPageContentWrapper = ({ children }: { children: React.ReactNode }) => (
    <div className="flex flex-col items-center justify-center flex-grow w-full bg-gradient-to-br from-background to-secondary/20 py-6">
      {children}
    </div>
  );


  if (isLoggedIn) {
    return (
      <LoginPageContentWrapper>
        <Card className="w-full max-w-md shadow-xl text-center">
          <CardHeader>
            <CardTitle className="text-2xl font-bold">您已登录</CardTitle>
            <CardDescription>您当前以 {sessionUsername} 的身份登录。</CardDescription>
          </CardHeader>
          <CardContent>
            <p>您想退出登录吗？或者前往仪表盘？</p>
          </CardContent>
          <CardFooter className="flex flex-col gap-3">
            <form action="/api/auth/logout" method="POST" onSubmit={handleLogout} className="w-full">
              <Button type="submit" variant="destructive" className="w-full">
                <LogOutIcon className="mr-2 h-5 w-5" /> 退出登录
              </Button>
            </form>
            <Button variant="outline" onClick={() => { startLoading(); router.push('/dashboard'); }} className="w-full">
              <LayoutDashboard className="mr-2 h-5 w-5" /> 前往仪表盘
            </Button>
          </CardFooter>
        </Card>
      </LoginPageContentWrapper>
    );
  }

  return (
    <LoginPageContentWrapper>
      <div className="w-full max-w-md">
        <Card className="shadow-xl">
          <CardHeader className="text-center">
            <CardTitle className="text-2xl font-bold">登录或注册 MineVent</CardTitle>
            <CardDescription>
              输入您的 Minecraft 用户名和密码进行登录。如果账户不存在，将会为您创建新账户。
            </CardDescription>
          </CardHeader>
          <form action={formAction}>
            <CardContent className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="minecraftUsername">Minecraft 用户名</Label>
                <Input
                  id="minecraftUsername"
                  name="minecraftUsername"
                  type="text"
                  placeholder="例如：Steve"
                  required
                  aria-describedby="username-error"
                />
                {state.errors?.minecraftUsername && <p id="username-error" className="text-sm text-destructive">{state.errors.minecraftUsername[0]}</p>}
              </div>

              <div className="space-y-2">
                <Label htmlFor="password">密码</Label>
                <Input
                  id="password"
                  name="password"
                  type="password"
                  placeholder="输入您的密码"
                  required
                  aria-describedby="password-error"
                />
                {state.errors?.password && <p id="password-error" className="text-sm text-destructive">{state.errors.password[0]}</p>}
                <p className="text-xs text-muted-foreground pt-1">
                  重要提示：此为原型系统，密码将以明文存储。请勿使用真实密码。
                </p>
              </div>

              <div className="space-y-2">
                <Label htmlFor="captcha">人机验证</Label>
                <div
                  id="captcha-placeholder"
                  className="w-full h-20 bg-muted rounded-md flex items-center justify-center text-muted-foreground text-sm"
                  aria-live="polite"
                >
                  人机验证区域 (例如：reCAPTCHA/Geetest)
                </div>
              </div>

              {state?.message && (!state.errors || Object.keys(state.errors).length === 0) && !state.success && (
                <p className="text-sm text-destructive flex items-center"><AlertCircle className="w-4 h-4 mr-1"/> {state.message}</p>
              )}
              
              <Button type="submit" className="w-full">
                <UserPlus className="mr-2 h-5 w-5" />
                登录 / 注册
              </Button>

              {isDebugMode && (
                <Button
                  type="button"
                  variant="secondary"
                  className="w-full border-dashed border-primary/50 hover:border-primary"
                  onClick={handleDebugLogin}
                >
                  <Eye className="mr-2 h-5 w-5" />
                  调试：以 dream_pep 身份查看仪表盘
                </Button>
              )}
            </CardContent>
          </form>
          <CardFooter className="flex flex-col gap-4">
            <p className="text-sm text-muted-foreground">
              遇到问题？请联系<LoadingLink href="#" className="font-medium text-primary hover:underline">管理员</LoadingLink>。
            </p>
          </CardFooter>
        </Card>
      </div>
    </LoginPageContentWrapper>
  );
}
